<template>
	<div>
		<div id="mapcointainer" ref="map"></div>
	</div>
</template>
<script type="text/javascript">
	let thisa = null;
	export default{
		data(){
			thisa = this;
			return {
				 tabPosition: 'bottom'

			}
		},
		mounted() {
				thisa.$refs.map.style.height=(window.innerHeight-131)/16+'em';
	      thisa.map = new AMap.Map('mapcointainer', {
	        resizeEnable: true,
	        zoom:15,

	        }
	      );
	      AMap.plugin(['AMap.ToolBar','AMap.Scale','AMap.OverView'],function(){
	        var toolBar = new AMap.ToolBar();
	        var scale = new AMap.Scale();
	        var OverView = new AMap.OverView({isOpen:true})
	        thisa.map.addControl(toolBar);
	        thisa.map.addControl(scale);
	        thisa.map.addControl(OverView);
   			})
	      thisa.map.plugin('AMap.Geolocation', function() {
	        let geolocation = new AMap.Geolocation({
	          enableHighAccuracy: true,
	          timeout: 10000,
	          buttonOffset: new AMap.Pixel(20, 10),
	          zoomToAccuracy: false,
	          buttonPosition:'RB'
	        });
	        thisa.map.addControl(geolocation);
	        function onComplete(data) {
	          // alert('定位成功');
	          // alert('经度:'+data.position.getLng()+",纬度:"+data.position.getLat());
	        }
	        function onError(data) {
	          alert('定位失败');
	        }
	        AMap.event.addListener(geolocation, 'complete', onComplete);//返回定位信息
	        AMap.event.addListener(geolocation, 'error', onError);      //返回定位出错信息
	    		geolocation.getCurrentPosition();
	      })
	  }
	}
</script>


